@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700;800;900&display=swap);
.table{width:75%;text-align:center;margin:0 auto}.standupBody{text-align:center;margin:0 auto}
body{font-family:"Kanit",sans-serif;background-color:#fff;color:#181c24;text-align:center;margin:0 auto}section{display:flex;margin-bottom:2rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0 auto}div{display:center}.button{border:1px solid #1e47ff;text-transform:uppercase;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:.3rem 1.5rem;background-color:#9ad3bc;border-color:#9ad3bc;color:#132743;font-weight:500;border-radius:.25rem;font-size:1rem;margin-right:.25rem}.button:hover{background-color:#2ec1ac;border-color:#2ec1ac;cursor:pointer}.button:focus{outline:none;border-color:#2ec1ac;background-color:#2ec1ac;color:#181c24}.button.addTask{font-size:3rem;padding:0 0 .4rem 0;border-radius:50%;width:2.5rem;height:2.5rem;line-height:1rem;margin-top:2rem}.button.moveTask{font-size:1.5rem;padding:.5rem .5rem;margin-bottom:0;line-height:1rem}.button.delete{background-color:#ffa5a5;border-color:#ffa5a5}.button.delete:hover{background-color:#ff8282}

.task{padding:1rem;margin-bottom:.5rem;display:flex;align-items:flex-end;border:1px solid #181c24;border-radius:.25rem;flex:1 1}.task.collapsedTask .moveTask{margin-bottom:.5rem;font-size:1.5rem;padding:.3rem .5rem}.task form{display:flex;flex-direction:column;text-align:center;align-items:center}.task form .input{background-color:transparent;border:1px solid transparent;border-radius:.25rem;color:#181c24;text-align:center;resize:none;display:flex}.task form .input:hover{cursor:pointer;border:1px solid #181c24}.task form .input:focus{outline:none;border-color:#1e47ff;color:#1e47ff}.task form .input::-webkit-input-placeholder{color:#181c24}.task form .input::placeholder{color:#181c24}.task form .title{margin-bottom:.5rem;padding:.5rem 0;width:100%;font-size:1.1rem}.task form .title:hover{cursor:text}.task form .description{margin-bottom:.5rem;padding:.5rem;width:100%;font-size:1.1rem}.task form .description:hover{cursor:text}.task form .urgencyLabels{margin-bottom:1.5rem;display:flex;flex-direction:row}.task form .urgencyLabels label{color:#132743;padding:.05rem .5rem;margin:0 .25rem;text-transform:uppercase;border-radius:.25rem}.task form .urgencyLabels label .icon{font-size:1.25rem;margin-right:.25rem;transition:all .4s ease-in-out}.task form .urgencyLabels label .icon.hide{display:none}.task form .urgencyLabels label .icon.show{display:inline-block}.task form .urgencyLabels label input{display:none}.task form .urgencyLabels label.low{background-color:#9ad3bc}.task form .urgencyLabels label.low:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.medium{background-color:#edf285}.task form .urgencyLabels label.medium:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.high{background-color:#ffa5a5}.task form .urgencyLabels label.high:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.selected{font-weight:700}.task form.collapsed{flex-direction:row;align-content:center}.task form.collapsed .title{margin:0}.task form.collapsed .title:hover{border-color:transparent;cursor:default}.task form.collapsed .urgencyLabels{margin-bottom:0;margin-right:1rem}.task form.collapsed .description,.task form.collapsed label{display:none}.task form.collapsed label.selected{display:inline-block;font-weight:500}.task form.collapsed .button{padding:.025rem .5rem}.task form.collapsed .button.moveTask{background-color:red}.task form.collapsed:hover .button{display:inline-block}
